@charset "UTF-8";
/* 圖片取代文字技巧 */
.navbar-brand .logo {
  background-image: linear-gradient(100deg, #EB9C37, #DD4C57);
  background-clip: text;
  color: transparent; }

.btnArea a:hover {
  box-shadow: 3px 3px 3px 2px rgba(0, 0, 0, 0.5); }

.btnArea a:active {
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.9);
  transition: .1s; }

.nav-item:hover {
  box-shadow: 5px 3px 3px 2px rgba(0, 0, 0, 0.3);
  border-radius: 15px; }

.nav-item:active {
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.9);
  transition: .1s; }
